* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-family: Roboto, sans-serif;
}

body {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.header {
  text-transform: uppercase;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 50px;
  font-size: 12px;
  font-weight: 500;
  display: flex;
}

.nav__link {
  color: #060b35;
  align-items: center;
  height: 60px;
  text-decoration: none;
  display: flex;
}

.logo-link {
  width: 40px;
  height: 40px;
}

.nav__link:hover {
  color: #00acdc;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  top: 56px;
}

.nav__list {
  list-style: none;
  display: flex;
}

.nav__item {
  margin: 0 10px;
}

.nav__item:first-child {
  margin-left: 0;
}

.nav__item:last-child {
  margin-right: 0;
}

.card-container {
  grid-template-rows: repeat(2, 408px);
  grid-template-columns: repeat(4, 200px);
  gap: 46px 48px;
  padding: 50px 40px;
  font-weight: 400;
  display: grid;
}

@media (width <= 487px) {
  .card-container {
    grid-template-rows: repeat(8, 408px);
    grid-template-columns: repeat(1, 200px);
    display: grid;
  }
}

@media (width >= 488px) and (width <= 767px) {
  .card-container {
    grid-template-rows: repeat(4, 408px);
    grid-template-columns: repeat(2, 200px);
    display: grid;
  }
}

@media (width >= 768px) and (width <= 1023px) {
  .card-container {
    grid-template-rows: repeat(3, 408px);
    grid-template-columns: repeat(3, 200px);
    display: grid;
  }
}

.card {
  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  align-items: center;
  width: 200px;
  padding: 0 16px;
  transition: transform .3s;
  display: flex;
}

.card__img {
  align-self: center;
  width: 100%;
  height: 134px;
  margin: 32px 0 40px;
  padding: 0 3px;
}

.card__title {
  color: #060b35;
  text-align: justify;
  width: 100%;
  margin: 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__text {
  margin: 0;
}

.card__text--one {
  color: #616070;
  font-size: 12px;
  line-height: 18px;
}

.card__text--two {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__product {
  color: #616070;
  text-align: justify;
  align-self: flex-start;
  width: 100%;
  margin: 4px 0 16px;
  font-size: 10px;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin: 24px 0 16px;
  display: flex;
}

.card__button {
  color: #fff;
  text-transform: uppercase;
  background: #00acdc;
  border: none;
  border-radius: 5px;
  width: 100%;
  height: 40px;
  margin-bottom: 16px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
  border-radius: 5px;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card__title {
  color: #34568b;
}

.stars {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin: 0;
  display: flex;
}

.stars__stars {
  justify-content: space-around;
  align-items: center;
  width: 96px;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars__stars--1 .stars__star:nth-child(-n+1), .stars__stars--2 .stars__star:nth-child(-n+2), .stars__stars--3 .stars__star:nth-child(-n+3), .stars__stars--4 .stars__star:nth-child(-n+4), .stars__stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.stars__text {
  color: #060b35;
  font-size: 10px;
  line-height: 14px;
}

.stars__card-text {
  margin: 0;
  line-height: 14px;
}

